@import "../../../styles/vendor/import/normalize";
.content {
    background-color: #f7f7f7;
}
.box {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding-top: 60px;
    flex-wrap: wrap;
    .card {
        width: 18%;
        display: flex;
        flex-direction: column;
        padding-bottom: 35px;
        @media (max-width: 768px) {
            width: 40%;
        }
        @media (max-width: 480px) {
            width: 75%;
        }
        &__img {
            width: max-content;
            align-self: center;
        }
        &__title {
            font-size: 18px;
            padding-top: 25px;
            text-align: center;
            color: $textTitleColor;
        }
        &__text {
            text-align: center;
            font-size: 14px;
            line-height: 1.71;
            color: #616161;
        }
    }
}
.line {
    border: none;
    width: 100%;
    height: 2px;
    margin: 25px 0 50px;
    background-color: #000000;
    color: #000000;
    opacity: 0.11;
}
.our-works__subtitle {
    @include subheaderStyle;
    padding-bottom: 22px;
    @media (max-width: 768px) {
        text-align: center;
        padding: 20px 0 40px;
    }
}
.our-works {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @media (max-width: 768px) {
        justify-content: center;
    }
    .block-works {
        width: 32%;
        @media (max-width: 768px) {
            width: 310px;
            padding-bottom: 45px;
        }
        &__img {
            width: 100%;
            height: auto;
            object-fit: cover;
        }
        &__title {
            font-size: 18px;
            padding-top: 20px;
            color: $textTitleColor; 
        }
        &__tech {
            font-size: 13px;
            line-height: 1.71;
            margin-top: 6px;
            color: #a8a8a8;
        }
        &__text {
            font-size: 14px;
            line-height: 2;
            color:#5c5c5c;
        }
    }
}
.quote {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 230px;
    background-color: #efefef;
    @media (max-width: 480px) {
        padding: 35px 0;
    }
    &__img {
        margin-bottom: 20px;
    }
    &__text {
        width: 760px;
        font-size: 20px;
        font-style: normal;
        font-weight: normal;
        text-align: center;
        line-height: 1.5;
        color: #a1a1a1;
        @media (max-width: 992px) {
            width: 80%;
        }
        @media (max-width: 768px) {
            width: 70%;
        }
    }
}