.footer {
    width: 100%;
    min-height: 460px;
    background-color: #1d1d1d;
}
.block {
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-top: 50px;
    margin-bottom: -50px;
    @media (max-width: 768px){
        justify-content: center;
    }
    .block-item {
        width: 24%;
        font-size: 14px;
        line-height: 1.71;
        text-align: left;
        color: #cacaca;
        @media (max-width: 768px){
            width: 90%;
            padding-bottom: 45px;
        }
        @media (max-width: 480px){
            width: 100%;
        }
        &__subtitle {
            @include subheaderStyle;
            color: #6c6c6c;
        }
        &__text {
            margin: 20px 0 0;
        }
        &__text-cont{
            margin: 20px 0;
        }
        &__contacts {
            display: flex;
            align-items: baseline;
            &__img{
                margin-right: 16px;
            }
            &__text{
                margin: 0;
            }   
        }
        &__twit{
            display: flex;
            align-items: baseline;
            &__img{
                margin-right: 14px;
                width: 15px;
                height: 11px;
            }
            &__text{
                margin: 20px 0 0;
            } 
        }
        &__date {
            margin-top: 0;
        }
    }
}
.footer {
    .wrapper {
        .line {
            border: none;
            width: 100%;
            height: 2px;
            margin: 50px 0;
            background-color: #fff;
            color: #fff;
            opacity: 0.11;
            @media (max-width: 768px){
                margin: 20px 0 50px;
            }
        }
        .reserv{
            font-size: 14px;
            text-align: left;
            padding-bottom: 45px;
            color: #cacaca;
            margin: 0;
        }
    }
}