.header__nav {
    width: 100%;
    height: 125px;
    background-color: #e5e5e5;
    align-items: center;
    @media (max-width: 768px) {
        height: 155px;
    }
    @media (max-width: 480px) {
        height: 125px;
    }
    .wrapper {
        height: inherit;
        .block-nav{
            display: flex;
            justify-content: space-between;
            align-items: center;
            height: inherit;
            @media (max-width: 768px) {
                flex-direction: column;
            }
            .subtitle {
                font-size: 40px;
                font-style: normal;
                font-weight: bold;
                color: $textTitleColor;
                @media (max-width: 768px) {
                    width: 100%;
                    padding-top: 30px;
                    text-align: center;
                    margin: 0;
                }
                @media (max-width: 480px) {
                    padding-top: 15px;
                }
            }
            .burger-menu {
                padding: 0 0 16px;
                @media (max-width: 768px) {
                    width: 131.5%;
                    height: 53px;
                    background-color: #555555;
                    padding-bottom: 0px;
                }
            }
            .nav {
                @media (max-width: 768px) {
                    height: 0px;
                    display: block;
                    flex-direction: column;
                    align-items: center;
                    position: absolute;
                    opacity: 0;
                    top: 102px;
                    z-index: 8;
                    width: 50%;
                    margin-left: calc(100% - 50%);
                    padding: 20px 0;
                    list-style: none;
                    text-align: center;
                    background-color: #555555;
                    box-shadow: 1px 0px 6px #909090;
                    @include transitionBurger;
                    max-height: 0;
                    font-size: 0;
                    overflow: hidden;
                    transition: 1s all;
                }
                @media (max-width: 480px) {
                    top: 72px;
                    width: 100%;
                    margin-left: 0;
                    z-index: 8;
                }
                &__items {
                    list-style-type: none;
                    display: inline-block;
                    @media (max-width: 768px) {
                        display: block;
                    }
                    .nav__items-link {
                        text-decoration: none;
                        @include subheaderStyle;
                        font-size: 16px;
                        padding-left: 23px;
                        @media (max-width: 992px) {
                            font-size: 15px;
                            padding-left: 8px;
                        }
                        @media (max-width: 768px) {
                            display: block;
                            padding: 12px 24px;
                            color: #e5e5e5;
                            font-size: 20px;
                            font-weight: 600; 
                        }
                    }
                    .nav__items-link:hover{
                        color: $colorHover;
                    }
                }
            }
        }
    }
}
#menu__toggle {
    opacity: 0;
}
@media (max-width: 768px) {
    .menu__btn {
        display: flex;
        align-items: center;
        position: absolute;
        top: 117px;
        right: 20px;
        width: 24px;
        height: 23px;
        cursor: pointer;
        z-index: 10;
        & > span,
        & > span::before,
        & > span::after {
            display: block;
            position: absolute;
            width: 100%;
            height: 5px;
            background-color: #aaaaaa;
            }
        &> span::before {
            content: '';
            top: -9px;
        }
        & > span::after {
            content: '';
            top: 9px;
        }
    }
    .nav__items:hover {
        background-color: #5d5d5d;
    }
    #menu__toggle:checked ~ .menu__btn > span {
        transform: rotate(45deg);
        @include transitionBurger; 
    }
    #menu__toggle:checked ~ .menu__btn > span::before {
        top: 0;
        transform: rotate(0);
        @include transitionBurger;
    }
    #menu__toggle:checked ~ .menu__btn > span::after {
        top: 0;
        transform: rotate(90deg);
        @include transitionBurger;
    }
    #menu__toggle:checked ~ .nav {
        max-height: 500px;
        height: 282px;
        opacity: 1;
        left: 0;
        @include transitionBurger;
        transition: height 0.5s;
    }
}
@media (max-width: 480px) {
    .menu__btn {
        top: 87px;
    }
}
.header-banner {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    width: 100%;
    height: 600px;
    @media (max-width: 1200px) {
        height: 440px;
    }
    @media (max-width: 992px) {
        height: 360px;
    }
    @media (max-width: 768px) {
        height: 270px;
    }
    @media (max-width: 480px) {
        height: 170px;
    } 
    &__img{
        width: 100%;
        height: inherit;
    }
    .wrapper {
        display: flex;
        align-items: center;
        height: inherit;
        position: absolute;
    }
    &__contain {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        // @media (max-width: 1200px){
        //     padding: 0 10%;
        // }
        // @media (max-width: 992px){
        //     padding: 0 8%;
        // }
        // @media (max-width: 768px){
        //     padding: 0 12%;
        // }
    }
}
