.header-banner {
    &__subtitle {
        max-width: 100%;
        font-size: 40px;
        text-align: center;
        color: #f3f3f3;
        @media (max-width: 1440px) {
            font-size: 35px;
        }
        @media (max-width: 1200px) {
            font-size: 30px;
        }
        @media (max-width: 992px) {
            font-size: 25px;
        }
        @media (max-width: 768px) {
            font-size: 21px;
        }
        @media (max-width: 480px) {
            font-size: 20px;
            line-height: 1.4;
        }
    }
    &__text {
        font-size: 32px;
        font-weight: bold;
        text-align: center;
        color: #a8a8a8;
        @media (max-width: 1200px) {
            font-size: 25px;
        }
        @media (max-width: 992px) {
            font-size: 21px;
        }
        @media (max-width: 768px) {
            font-size: 19px;
        }
        @media (max-width: 480px) {
            display: none;
        }
    }
    &__button {
        width: 140px;
        height: 45px;
        border-radius: 3px;
        border: none;
        @include subheaderStyle(14px, #ffffff, normal);
        text-align: center;
        background-color: #3f8dbf;
        cursor: pointer;
        outline: none;
        @media (max-width: 480px) {
            height: 35px;
            margin-top: 20px;
        }
    }
}