@mixin subheaderStyle($fsize: 18px, $fcolor: $textTitleColor, $fweight: bold) {
    font-size: $fsize;
    color: $fcolor;
    font-weight: $fweight;
}
@mixin alignment($display: flex, $alignItems: center, $justifyContent: center) {
    display: $display;
    align-items: $alignItems;
    justify-content: $justifyContent;
}
@mixin transitionBurger($property: (translateY(100%))) {
    -webkit-transition: $property;
     -moz-transition: $property;
     -o-transition: $property;
     transition: $property;
}